import React, { Fragment, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addEducation } from '../../actions/profile'


const AddEducation = ({ addEducation, history }) => {
    const [formData, setFormData] = useState ({
        school:'',
        degree:'',
        fieldofstudy:'',
        from:'',
        to:'',
        current:'',
        description:''
    });

    const [toDateDisable, toggleDisable] = useState(false);

    const { school, degree, fieldofstudy, from, to, current, description } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    return (
        <Fragment>
            <h1 class="large text-primary">
       Add Your symptoms/ Treatment
      </h1>
      <p class="lead">
      <i class="fas fa-capsules"></i> MAKE YOUR DAILY ROUTINE EASIER USING SELFQ
      </p>
      <small>* required field</small>
      <form class="form" onSubmit={e =>{ 
          e.preventDefault();
          addEducation(formData, history)
      }}>
        <div class="form-group">
          <input type="text" placeholder="* Symptom/ Treatment" name="school" value={school} onChange={e => onChange(e)} required />
        </div>
        <div class="form-group">
          <input type="text" placeholder="* Remarks" name="degree" value={degree} onChange={e => onChange(e)} required />
        </div>
         
        <div class="form-group">
          <h4>From Date</h4>
          <input type="date" name="from" value={from} onChange={e => onChange(e)} />
        </div>
         <div class="form-group">
          <p><input type="checkbox" name="current" value={current} onChange={e => {
              setFormData({...formData, current:!current});
              toggleDisable(!toDateDisable);
          }}  /> {','}Current symtom/</p>
        </div>
        <div class="form-group">
          <h4>To Date</h4>
          <input type="date" name="to" value={to} onChange={e => onChange(e)} disabled={toDateDisable ? 'disabled' :''}  />
        </div>
        <div class="form-group">
          <textarea
            name="description"
            cols="30"
            rows="5"
            placeholder=" Other Description"
            value={description} onChange={e => onChange(e)}
          ></textarea>
        </div>
        <input type="submit" class="btn btn-primary my-1" />
        <Link class="btn btn-light my-1" to="/dashboard">Go Back</Link>
      </form> 
        </Fragment>
    )
}

AddEducation.propTypes = {
    addEducation: PropTypes.func.isRequired,
}

export default connect (null, { addEducation })(withRouter(AddEducation));
