import React, { Fragment, useEffect } from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import Spinner from '../layout/Spinner'
import ProfileTop from './ProfileTop'
import ProfileAbout from './ProfileAbout'
import ProfileExperience from './ProfileExperience'
import ProfileEducation from './ProfileEducation'
import ProfileMessages from './ProfileMessages'
import AddedMessages from './addedMessages'
import {getProfileByID} from '../../actions/profile'

const Profile = ({ getProfileByID, profile:{ profile, loading },auth, match }) => {
    useEffect(() => {
        getProfileByID(match.params.id);
    },[getProfileByID, match.params.id])

    return (
    <Fragment>
        {profile === null || loading ? ( <Spinner /> ) : (<Fragment>
            <Link to='/profiles' className='btn btn-light' >
                Back to profiles
            </Link>
            {auth.isAuthenticated && auth.loading === false && auth.user._id === profile.user._id &&
             (<Link to='/edit-profile' className='btn btn-dark'>Edit Profile</Link>)}
             <div class="profile-grid my-1">
            <ProfileTop profile={profile} />
            <ProfileAbout profile={profile}/>
            {/* <div className="profile-exp bg-white pp-2">
                <h2 className="text-primary">Experience</h2>
                {profile && profile.experience.length > 0 ? (<Fragment>
                    {profile.experience.map(experience => (
                        <ProfileExperience
                        key={experience._id}
                        experience={experience}
                        />
                    ))}
                </Fragment>) : (<h4>No Experience credentials</h4>)}
            </div> */}

            {/* <div className="profile-edu bg-white pp-2">
                <h2 className="text-primary">Education</h2>
                {profile && profile.education.length > 0 ? (<Fragment>
                    {profile.experience.map(education => (
                        <ProfileEducation
                        key={education._id}
                        education={education}
                        />
                    ))}

                

                </Fragment>) : (<h4>No Experience credentials</h4>)}
                
            </div> */}
            <div>
            <AddedMessages profile={profile}/>
            <ProfileMessages profile={profile}/>
            </div>
        </div>
            </Fragment>
        )}
        

       
           
    
    </Fragment>
    );   
    
}

Profile.propTypes = {
    getProfileByID: PropTypes.func.isRequired,
    profile:PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    profile: state.profile,
    auth: state.auth
})

export default connect(mapStateToProps, { getProfileByID })(Profile)
