import React, { Fragment, useState, useEffect } from 'react'
import { Link, withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { createProfile, getCurrentProfile } from '../../actions/profile'

const EditProfile = ({ 
    profile: { profile, loading }, 
    createProfile, getCurrentProfile, 
    history 
}) => {

const [formData,setFormData] = useState({
    company:'',
    website:'',
    location:'',
    bio:'',
    status:'',
    githubusername:'',
    skills:'',
    youtube:'',
    facebook:'',
    twitter:'',
    instargram:'',
    Linkedin:''
});

const [displaySocialInputs, toggleSocialInputs] = useState(false);

useEffect(() => {
    getCurrentProfile();

    setFormData({
    company: loading || !profile.company ? '' : profile.company,
    website: loading || !profile.website ? '' : profile.website,
    location: loading || !profile.location ? '' : profile.location,
    status: loading || !profile.company ? '' : profile.status,
    skills: loading || !profile.skills ? '' : profile.skills.join(','),
    githubusername: loading || !profile.githubusername ? '' : profile.githubusername,
    bio: loading || !profile.bio ? '' : profile.bio,
    twitter: loading || !profile.social ? '' : profile.social.twitter,
    facebook: loading || !profile.social ? '' : profile.social.facebook,
    linkedin: loading || !profile.social ? '' : profile.social.linkedin,
    youtube: loading || !profile.social ? '' : profile.social.youtube,
    instagram: loading || !profile.social ? '' : profile.social.instagram
 });
}, [loading, getCurrentProfile]);

const { 
    company,
        website,
        location,
        bio,
        status,
        githubusername,
        skills,
        youtube,
        facebook,
        twitter,
        instargram,
        linkedin
} = formData;

const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

const onSubmit = e => { e.preventDefault();
    createProfile(formData, history, true);
}


return (
    <Fragment>
         <h1 className="large text-primary">
        Create Your Profile
      </h1>
      <p className="lead">
        <i className="fas fa-user"></i> Let's get some information to make your
        profile stand out
      </p>
      <small>* = required field</small>
      <form className="form" onSubmit={e => onSubmit(e)}>
        <div className="form-group">
        <select name="status" value={status} onChange={e => onChange(e)}>
        <option value="0">* Select Professional Status</option>
            <option value="Patient">Patient</option>
            <option value="Family Physician">Family Physician</option>
            <option value="Internal Medicine Physician">Internal Medicine Physician</option>
            <option value="Pediatrician">Pediatrician</option>
            <option value="Obstetrician/Gynecologis">Obstetrician/Gynecologist (OB/GYN)</option>
            <option value="Surgeon">Surgeon</option>
            <option value="Psychiatrist">Psychiatrist</option>
            <option value="Cardiologist">Cardiologist</option>
            <option value="Neurologist">Neurologist</option>
            
          </select>
          <small className="form-text"
            >Please select one</small>
        </div>
        <div className="form-group">
          <input type="text" placeholder="Hospital" name="company" value={company} onChange={e => onChange(e)} />
          <small className="form-text"
            >Please enter your hospital</small
          >
        </div>
        {/* <div className="form-group">
          <input type="text" placeholder="Website" name="website" value={website} onChange={e => onChange(e)}/>
          <small className="form-text"
            >Could be your own or a company website</small
          >
        </div> */}
        <div className="form-group">
          <input type="text" placeholder="Location" name="location" value={location} onChange={e => onChange(e)} />
          <small className="form-text"
            >please enter your living area  </small
          >
        </div>
        {/* <div className="form-group">
          <input type="text" placeholder="* Special area to treatment" name="skills" value={skills} onChange={e => onChange(e)} />
          <small className="form-text"
            >Please use comma separated values (eg.
            HTML,CSS,JavaScript,PHP)</small
          >
        </div> */}
        {/* <div className="form-group">
          <input
            type="text"
            placeholder="Github Username"
            name="githubusername"
            value={githubusername} onChange={e => onChange(e)}
          />
          <small className="form-text"
            >If you want your latest repos and a Github link, include your
            username</small
          >
        </div> */}
        <div className="form-group">
          <textarea placeholder="A short bio of yourself" name="bio" value={bio} onChange={e => onChange(e)}></textarea>
          <small className="form-text">Tell us a little about yourself</small>
        </div>

        <div className="my-2">
          <button onClick={() => toggleSocialInputs(!displaySocialInputs)} type="button" className="btn btn-light">
            Add Social Network Links
          </button>
          <span>Optional</span>
        </div>

        {displaySocialInputs && <Fragment>

                <div className="form-group social-input">
                <i class="fas fa-phone-alt fa-2x"></i>
          <input type="text" placeholder="Contact No" name="twitter" value={twitter} onChange={e => onChange(e)} />
        </div>

        <div className="form-group social-input">
          <i className="fab fa-facebook fa-2x"></i>
          <input type="text" placeholder="Facebook URL" name="facebook" value={facebook} onChange={e => onChange(e)} />
        </div>

        {/* <div className="form-group social-input">
          <i className="fab fa-youtube fa-2x"></i>
          <input type="text" placeholder="YouTube URL" name="youtube" value={youtube} onChange={e => onChange(e)} />
        </div> */}

        <div className="form-group social-input">
        <i class="far fa-envelope fa-2x"></i>
          <input type="text" placeholder="gmail" name="linkedin" value={linkedin} onChange={e => onChange(e)} />
        </div>

        {/* <div className="form-group social-input">
          <i className="fab fa-instagram fa-2x"></i>
          <input type="text" placeholder="Instagram URL" name="instagram" value={instargram} onChange={e => onChange(e)} />
        </div> */}

        </Fragment>}

        <input type="submit" className="btn btn-primary my-1" />
        <Link  className="btn btn-light my-1" to='/dashboard'>Go Back</Link>
      </form>
    </Fragment>
)
   
}

EditProfile.propTypes = {
    createProfile: PropTypes.func.isRequired,
    getCurrentProfile: PropTypes.func.isRequired,
    profile:PropTypes.object.isRequired
    
};

const mapStateToProps = state => ({
    profile: state.profile
})

export default connect(
    mapStateToProps, 
    { createProfile, getCurrentProfile }
    )(withRouter(EditProfile))

